"use client";

import { useState, useEffect } from "react";
import { TrackMapWithBoundaries } from "../components/TrackMapWithBoundaries";
import { TelemetryGraphs } from "./components/TelemetryGraphs";
import { ReplayScrubber } from "./components/ReplayScrubber";

export default function LapAnalysisPage() {
  const [availableLaps, setAvailableLaps] = useState<any[]>([]);
  const [selectedLap, setSelectedLap] = useState<number | null>(null);
  const [lapData, setLapData] = useState<any>(null);
  const [corners, setCorners] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);
  const [playbackSpeed, setPlaybackSpeed] = useState(1);

  useEffect(() => {
    fetch("/api/laps")
      .then((res) => res.json())
      .then((data) => {
        console.log("Laps loaded:", data);
        setAvailableLaps(data.laps);
        if (data.laps.length > 0) {
          setSelectedLap(data.laps[0].lapNumber);
        }
        setLoading(false);
      })
      .catch((err) => {
        console.error("Failed to load laps:", err);
        setLoading(false);
      });
  }, []);

  useEffect(() => {
    if (selectedLap === null) return;

    fetch(`/api/laps/${selectedLap}`)
      .then((res) => res.json())
      .then((data) => {
        console.log("Lap data loaded:", data);
        setLapData(data);
      })
      .catch((err) => console.error("Failed to load lap:", err));
  }, [selectedLap]);

  useEffect(() => {
    if (!lapData?.metadata?.trackName) return;

    const trackName = lapData.metadata.trackName;
    const match = trackName.match(/\(([^)]+)\)/);
    const simpleName = match ? match[1].toLowerCase() : trackName.toLowerCase();

    console.log("Loading corners for:", simpleName);
    fetch(`/api/corners/${simpleName}`)
      .then((res) => res.json())
      .then((data) => {
        console.log("Corners loaded:", data);
        setCorners(data.corners || []);
      })
      .catch((err) => console.error("Failed to load corners:", err));
  }, [lapData]);

  if (loading) {
    return (
      <div className="flex h-screen items-center justify-center bg-zinc-950">
        <div className="text-center">
          <div className="mb-4 text-4xl">‚è±Ô∏è</div>
          <div className="text-xl text-zinc-400">Loading telemetry data...</div>
        </div>
      </div>
    );
  }

  if (availableLaps.length === 0) {
    return (
      <div className="flex h-screen items-center justify-center bg-zinc-950">
        <div className="text-center">
          <div className="mb-4 text-4xl">üèéÔ∏è</div>
          <div className="mb-2 text-2xl text-white">No Laps Recorded</div>
          <div className="text-zinc-400">
            Start the UDP listener and drive some laps in F1 2024
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-zinc-950">
      <div className="border-b border-zinc-800 bg-zinc-900/50 backdrop-blur-sm">
        <div className="container mx-auto px-6 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-2xl font-bold text-white">
                2D Lap Analysis
              </h1>
              <p className="text-sm text-zinc-400">
                {lapData?.metadata?.trackName || "Loading..."}
                {lapData?.metadata?.carName && ` ‚Ä¢ ${lapData.metadata.carName}`}
              </p>
            </div>

            <div className="flex items-center gap-2">
              <label className="text-sm font-medium text-zinc-400">Lap</label>
              <select
                value={selectedLap || ""}
                onChange={(e) => setSelectedLap(Number(e.target.value))}
                className="rounded-lg bg-zinc-800 px-3 py-2 text-white border border-zinc-700 focus:border-cyan-500 focus:outline-none"
              >
                {availableLaps.map((lap) => (
                  <option key={lap.lapNumber} value={lap.lapNumber}>
                    Lap {lap.lapNumber} ({lap.lapTime})
                  </option>
                ))}
              </select>
            </div>
          </div>
        </div>
      </div>

      <div className="mx-auto px-6 py-6 max-w-[1800px]">
        <div className="space-y-6">
          <TrackMapWithBoundaries
            lapData={lapData}
            corners={corners}
            currentIndex={currentIndex}
            trackName="sakhir-(bahrain)"
          />

          <ReplayScrubber
            lapData={lapData}
            currentIndex={currentIndex}
            isPlaying={isPlaying}
            playbackSpeed={playbackSpeed}
            onIndexChange={setCurrentIndex}
            onPlayPause={() => setIsPlaying(!isPlaying)}
            onSpeedChange={setPlaybackSpeed}
          />
        </div>
      </div>
    </div>
  );
}
