"use client";

import { useEffect, useRef } from "react";

interface ReplayScrubberProps {
  lapData: any;
  currentIndex: number;
  isPlaying: boolean;
  playbackSpeed: number;
  onIndexChange: (index: number) => void;
  onPlayPause: () => void;
  onSpeedChange: (speed: number) => void;
}

export function ReplayScrubber({
  lapData,
  currentIndex,
  isPlaying,
  playbackSpeed,
  onIndexChange,
  onPlayPause,
  onSpeedChange,
}: ReplayScrubberProps) {
  const animationFrameRef = useRef<number | null>(null);
  const lastTimeRef = useRef<number>(0);

  useEffect(() => {
    if (!isPlaying || !lapData?.telemetry) return;

    const animate = (currentTime: number) => {
      if (!lastTimeRef.current) {
        lastTimeRef.current = currentTime;
      }

      const deltaTime = (currentTime - lastTimeRef.current) / 1000; // Convert to seconds

      if (deltaTime >= 0.016) { // ~60fps
        onIndexChange((prev: number) => {
          if (prev >= lapData.telemetry.length - 1) {
            return 0;
          }

          // Calculate how many points to advance based on real time
          // Each telemetry point represents ~1 meter, car speed in km/h
          const currentPoint = lapData.telemetry[Math.floor(prev)];
          const speed = Math.max(currentPoint?.speed || 100, 30); // km/h, minimum 30 to prevent too slow braking
          const metersPerSecond = (speed * 1000) / 3600; // Convert km/h to m/s
          const pointsToAdvance = metersPerSecond * deltaTime * playbackSpeed;

          return Math.min(prev + pointsToAdvance, lapData.telemetry.length - 1);
        });
        lastTimeRef.current = currentTime;
      }

      animationFrameRef.current = requestAnimationFrame(animate);
    };

    animationFrameRef.current = requestAnimationFrame(animate);

    return () => {
      if (animationFrameRef.current !== null) {
        cancelAnimationFrame(animationFrameRef.current);
      }
      lastTimeRef.current = 0;
    };
  }, [isPlaying, playbackSpeed, lapData, onIndexChange]);

  if (!lapData?.telemetry) return null;

  const totalPoints = lapData.telemetry.length;
  const currentPoint = lapData.telemetry[Math.floor(currentIndex)];
  const progress = (currentIndex / totalPoints) * 100;

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = (seconds % 60).toFixed(1);
    return `${mins}:${secs.padStart(4, '0')}`;
  };

  return (
    <div className="rounded-xl border border-zinc-800 bg-zinc-900/50 p-4">
      <div className="mb-3 flex items-center justify-between">
        <div className="flex items-center gap-4">
          <button
            onClick={onPlayPause}
            className="flex h-10 w-10 items-center justify-center rounded-lg bg-cyan-500 text-black hover:bg-cyan-400 transition-colors"
          >
            {isPlaying ? (
              <svg className="h-5 w-5" fill="currentColor" viewBox="0 0 24 24">
                <rect x="6" y="4" width="4" height="16" />
                <rect x="14" y="4" width="4" height="16" />
              </svg>
            ) : (
              <svg className="h-5 w-5" fill="currentColor" viewBox="0 0 24 24">
                <path d="M8 5v14l11-7z" />
              </svg>
            )}
          </button>

          <div className="flex items-center gap-2">
            <span className="text-sm text-zinc-400">Speed:</span>
            {[0.5, 1, 2, 4].map((speed) => (
              <button
                key={speed}
                onClick={() => onSpeedChange(speed)}
                className={`rounded px-2 py-1 text-sm transition-colors ${
                  playbackSpeed === speed
                    ? "bg-cyan-500 text-black"
                    : "bg-zinc-800 text-zinc-400 hover:bg-zinc-700"
                }`}
              >
                {speed}x
              </button>
            ))}
          </div>
        </div>

        <div className="flex items-center gap-6 text-sm">
          <div>
            <span className="text-zinc-400">Distance: </span>
            <span className="font-mono text-white">
              {Math.round(currentPoint?.distance || 0)}m
            </span>
          </div>
          <div>
            <span className="text-zinc-400">Speed: </span>
            <span className="font-mono text-white">
              {Math.round(currentPoint?.speed || 0)} km/h
            </span>
          </div>
          <div>
            <span className="text-zinc-400">Time: </span>
            <span className="font-mono text-white">
              {formatTime(currentPoint?.time || 0)}
            </span>
          </div>
          <div>
            <span className="text-zinc-400">Gear: </span>
            <span className="font-mono text-white">
              {currentPoint?.gear || 0}
            </span>
          </div>
        </div>
      </div>

      <div className="relative pt-2 pb-1">
        <input
          type="range"
          min="0"
          max={totalPoints - 1}
          value={currentIndex}
          onChange={(e) => onIndexChange(Number(e.target.value))}
          className="w-full h-2 bg-zinc-800 rounded-full appearance-none cursor-pointer
            [&::-webkit-slider-thumb]:appearance-none
            [&::-webkit-slider-thumb]:w-4
            [&::-webkit-slider-thumb]:h-4
            [&::-webkit-slider-thumb]:rounded-full
            [&::-webkit-slider-thumb]:bg-cyan-500
            [&::-webkit-slider-thumb]:cursor-pointer
            [&::-webkit-slider-thumb]:shadow-lg
            [&::-webkit-slider-thumb]:shadow-cyan-500/50
            [&::-moz-range-thumb]:w-4
            [&::-moz-range-thumb]:h-4
            [&::-moz-range-thumb]:rounded-full
            [&::-moz-range-thumb]:bg-cyan-500
            [&::-moz-range-thumb]:border-0
            [&::-moz-range-thumb]:cursor-pointer
            [&::-moz-range-thumb]:shadow-lg
            [&::-moz-range-thumb]:shadow-cyan-500/50"
          style={{
            background: `linear-gradient(to right, rgb(6 182 212) 0%, rgb(6 182 212) ${progress}%, rgb(39 39 42) ${progress}%, rgb(39 39 42) 100%)`
          }}
        />
      </div>

      <div className="mt-2 flex justify-between text-xs text-zinc-500">
        <span>0m</span>
        <span>{Math.round(lapData.telemetry[totalPoints - 1]?.distance || 0)}m</span>
      </div>
    </div>
  );
}
